<script>
import $ from 'jquery'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import { mapGetters } from 'vuex'

export default {
  data () {
    return {
      swiperAllProducts: {
        slidesPerView: 5,
        spaceBetween: 50,
        loopFillGroupWithBlank: true,
        height: 300,
        loop: true,
        speed: 1000,
        // init: false,
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        },
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        },
        breakpoints: {
          1024: {
            slidesPerView: 4,
            spaceBetween: 40
          },
          768: {
            slidesPerView: 3,
            spaceBetween: 30
          },
          640: {
            slidesPerView: 2,
            spaceBetween: 20
          },
          320: {
            slidesPerView: 1,
            spaceBetween: 10
          }
        }
      },
      // swiper
      swiperBanner: {
        autoplay: {
          disableOnInteraction: false, // 
          delay: 4000 //  
        }, // 
        speed: 3000, // sliderms
        loop: true, //  
        grabCursor: false, // trueSwiper
        // setWrapperSize: true, //Swiperflexbox(display: flex)Wrapperslidesflexbox
        autoHeight: true, //  truewrappercontainerslide
        scrollbar: '.swiper-scrollbar', //  
        mousewheelControl: true, // trueslide
        observeParents: false, // swiper/swiperswiper

        pagination: {
          el: '.swiper-pagination',
          // type : 'progressbar', // 
          clickable: true //  Swiper
        },
        navigation: {
          // nextEl,prevElnavigation
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        }
      },
      aboutUsImg: [
        //  require  `  () key  0 
        {
          url: require('../assets/img-1.png'),
          title: '',
          content:
            'Gathering good designers everywhere in the world,includes so many kinds of art and handmade culture.',
          key: 'card-1'
        },
        {
          url: require('../assets/img-2.png'),
          title: '',
          content:
            'After our team review and choice , we do our best to give what you guys deserve the good design.',
          key: 'card-2'
        },
        {
          url: require('../assets/img-3.png'),
          title: '',
          content:
            'Be a good bridge between designer and customers,good buy for good life.',
          key: 'card-3'
        }
      ],
      SwiperImgs: [
        {
          imgurl:
            require('../assets/banner-1.jpg'),
          key: 1
        },
        {
          imgurl:
          require('../assets/banner-2.jpg'),
          key: 2
        },
        {
          imgurl:
            require('../assets/banner-3.jpg'),
          key: 3
        }
      ],
      swiperOnSale: {
        slidesPerView: 5,
        spaceBetween: 50,
        autoHeight: true,
        loopFillGroupWithBlank: true,
        height: 300,
        loop: true,
        speed: 1000,
        // init: false,
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        },
        breakpoints: {
          1024: {
            slidesPerView: 4,
            spaceBetween: 40
          },
          768: {
            slidesPerView: 3,
            spaceBetween: 30
          },
          640: {
            slidesPerView: 2,
            spaceBetween: 20
          },
          320: {
            slidesPerView: 1,
            spaceBetween: 10
          }
        },
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        }
      },
      fullWidth: 0,
      fullHeight: 0
    }
  },
  components: {
    swiper,
    swiperSlide
  },
  methods: {
    getWindow () {
      $(window).scroll(function () {
        let scrollPos = $(window).scrollTop()
        // console.log(scrollPos)
        $('.aboutus').each(function () {
          let card = document.getElementById('card-1')
          let cardPos = card.getBoundingClientRect().top
          if (scrollPos >= cardPos) {
            $('.coupon_pop_1').addClass('fadeIn')
            setTimeout(() => {
              $('.coupon_pop_2').addClass('fadeIn')
            }, 1000)
            setTimeout(() => {
              $('.coupon_pop_3').addClass('fadeIn')
            }, 2000)
            $('#card-1').addClass('fadeIn')
            setTimeout(() => {
              $('#card-2').addClass('fadeIn')
            }, 750)
            setTimeout(() => {
              $('#card-3').addClass('fadeIn')
            }, 1500)
          }
        })
      })
    },
    getCoupon () {
      const vm = this
      const api = `${process.env.VUE_APP_APIPATH}/api/user/check`
      vm.$http.post(api).then(response => {
        if (response.data.success) {
          $('#getCoupon').modal('show')
        } else {
          this.$store.dispatch('updateMessage', { message: response.data.message, status: 'mistake' })
        }
      })
    },
    getProducts () {
      this.$store.dispatch('getProducts')
    },
    openProduct (id) {
      let routerPush = this.$router.push({
        path: '/productpage',
        query: { id: id }
      })
      console.log(routerPush)
    },
    addCart (id) {
      this.$store.dispatch('addCart', id)
    },
    addFavor (e) {
      this.$store.dispatch('addFavor', e)
    }
  },
  computed: {
    ...mapGetters(['onsale', 'favoProduct', 'products', 'isLoading',
      'coupopforcustom', 'origin_products'])
  },
  created () {
    this.$store.dispatch('refresh')
    this.getProducts()
    this.getWindow()
  },
  mounted () {
    setTimeout(() => {
      $('.banner_log').addClass('fadeIn')
    }, 600)
  }
}
</script>

